@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap);
:root{--primary-color: rgb(93, 5, 167);--primary-color-80: rgba(93, 5, 167, .8);--button-shadow: rgba(17, 12, 46, 0.25) 0 4px 8px;--center-shadow: rgba(17, 12, 46, 0.15) 0 0 24px 0px;--header-shadow: rgba(17, 12, 46, 0.15) 0px 12px 24px 0px;--modal-shadow: rgba(17, 12, 46, 0.25) 0px 48px 100px 0px;--pizza-shadow: rgba(177, 116, 10, 0.6) 0px 4px 36px;--card-shadow: rgba(17, 12, 46, 0.15) 0px 12px 24px 0px;--card-shadow-hover: rgba(17, 12, 46, 0.075) 0px 24px 32px 0px;--bounce: cubic-bezier(0.34, 1.56, 0.64, 1) }html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}*:focus,*:before:focus,*:after:focus{outline:auto 5px -webkit-focus-ring-color}body{margin:0;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#f8f8ff;min-height:100vh;margin:0 auto;display:flex;flex-direction:column;align-items:center;justify-content:center;color:#333;box-sizing:border-box}body #root{position:relative;min-height:100vh;max-width:1100px;padding:0 1rem;width:100%;background-color:#fff}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
